@import '../../styles/variables';
@import '../../styles/mixins';

.list-composer {
  flex: none;
  width: 272px;
  min-width: 272px;
  margin: 0 6px;
  box-sizing: border-box;
  height: auto;
  display: flex;
  flex-flow: column wrap;
  background-color: $haze;
  border-radius: 4px;
  padding: 8px;
  &.is-active {
    .list-composer__input {
      background-color: rgba($white, 0.6);
      margin-bottom: 8px;
    }
  }
  &.is-list-layout {
    width: 100%;
    margin: 0;
    background-color: transparent;
    border-radius: 0;
    // box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.15);
    .list-composer__input {
      border: none;
      margin-bottom: 0;
      padding: 8px 4px;
      background-color: transparent;
      &::placeholder {
        color: $primary;
      }
    }
    .list-composer__form {
      display: flex;
      border-radius: 4px;
      padding: 6px 0;
      border: 1px solid transparent;
    }
    .list-composer__footer {
      margin-left: 4px;
      flex: none;
    }
    .list-composer__btn {
      padding: 8px 6px;
    }
    .list-composer__btn--icon {
      border: none;
      width: 38px;
      height: 38px;
      .feather {
        width: 18px;
        height: 18px;
      }
    }
    &.is-active {
      .list-composer__input {
        &::placeholder {
          color: $grey-600;
        }
      }
      .list-composer__form {
        border-color: $grey-600;
        box-shadow: inset 0 1px $grey-300;
        padding: 4px;
      }
    }
    @include md {
      padding: 8px 12px;
    }
  }
  &.is-board-layout {
    &:after {
      content: '';
      background-color: transparent;
      height: 1px;
      width: 8px;
      position: relative;
      left: calc(100% + 14px);
      top: 0;
    }
    .list-composer__btn--add {
      &:disabled {
        background-color: $grey-400;
        border-color: $grey-500;
        color: $grey-600;
      }
    }
  }
}

.list-composer__input {
  background-color: transparent;
  height: auto;
  border-color: transparent;
  padding: 5px 8px;
}

.list-composer__btn--add {
  margin: 0;
  vertical-align: top;
}

.list-composer__btn {
  font-size: 14px;
  padding: 8px;
}
