@import '../../styles/variables';
@import '../../styles/mixins';

.avatar {
  text-align: center;
  font-family: $heading-font;
  box-sizing: border-box;
  flex: none;
  display: inline-block;
  &.is-online:before {
    content: '';
    position: absolute;
    right: 0;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: $success;
    border: 1px solid $white;
    box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075);
  }
}

.avatar--md {
  width: 40px;
  height: 40px;
  line-height: 38px;
  font-size: 14px;
}

.avatar--sm {
  width: 24px;
  height: 24px;
  line-height: 22px;
  font-size: 11px;
}

.avatar--lg {
  width: 64px;
  height: 64px;
  line-height: 64px;
  font-size: 20px;
}

.avatar--circle {
  border-radius: 50%;
  .avatar__placeholder {
    border-radius: 50px;
  }
}

.avatar--square {
  border-radius: none;
}

.avatar--rounded {
  border-radius: 4px;
  .avatar__placeholder {
    border-radius: 4px;
  }
}

.avatar__img {
  max-width: 100%;
  max-height: 100%;
  vertical-align: middle;
}

.avatar__placeholder {
  background-color: $secondary;
  border: 1px solid $secondary-dk;
  color: $white;
  width: 100%;
  height: 100%;
  vertical-align: middle;
  box-sizing: border-box;
  display: inline-block;
}
